@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.article_shop-info_table_heading--pc{
    font-family: va.$serif;
    color: #fff;
    font-weight: 500;
    font-size: max(calc(20/va.$sp-design-width*100vw),20px);
    line-height: calc(38/26);
    letter-spacing: calc(100/1000*1em);
    margin-bottom: max(calc(40/va.$sp-design-width*100vw),40px);
    display: inline-block;
    display: none;
    //height: max(calc(80/va.$sp-design-width*100vw),80px);
    @include mixin.mq(pc){
        font-size:max(calc(26/va.$pc-design-width*100vw),26px);
        margin-bottom: max(calc(38/va.$pc-design-width*100vw),38px);
        line-height: calc(37/26);
    }
}