@use './vars' as va;

$breakpoint: (
  sp: 'screen and (max-width: 767px)',
  tb: 'screen and (min-width: 768px)',
  pc: 'screen and (min-width: 1025px)'
);

@mixin mq($bp) {
  @media #{map-get($breakpoint, $bp)} {
   @content;
  }
}

@mixin nav-item--Japanese($fontFamily:va.$serif,$fontColor:va.$text-color){
  font-family: $fontFamily;
  color: $fontColor;
  font-size: max(calc(18/va.$pc-design-width*100vw),18px);
  line-height: calc(24/18);
  text-align: center;
  font-weight: 500;
  writing-mode: vertical-lr;
}

@mixin nav-item--English($fontFamily:va.$english,$fontColor:va.$text-color){
  font-family: $fontFamily;
  color: $fontColor;
  font-size: max(calc(10/va.$pc-design-width*100vw),10px);
  line-height: calc(13/10);
  letter-spacing: calc(100/1000*1em);
  //text-align: center;
  font-weight: 500;
  opacity: .7;
  text-transform: uppercase;
  margin-top: max(calc(26/va.$pc-design-width*100vw),26px);
}

@mixin sidebar-item--Japanese($fontFamily:va.$serif,$fontColor:va.$sub-color){
  font-family: $fontFamily;
  color: $fontColor;
  font-size: max(calc(18/va.$pc-design-width*100vw),18px);
  //line-height: calc(26/18);
  letter-spacing: calc(400/1000*1em);
  text-align: center;
  font-weight: 500;
  writing-mode: vertical-lr;
}

@mixin sidebar-item--English($fontFamily:va.$english,$fontColor:va.$sub-color){
  font-family: $fontFamily;
  color: $fontColor;
  font-size: max(calc(10/va.$pc-design-width*100vw),10px);
  line-height: calc(13/10);
  letter-spacing: calc(100/1000*1em);
  //text-align: center;
  font-weight: 500;
  opacity: .7;
  text-transform: uppercase;
  margin-top: max(calc(26/va.$pc-design-width*100vw),26px);
}

@mixin button-sub-color($fontFamily:va.$serif,$fontColor:va.$text-color,$backgroudColor:va.$sub-color){
  font-family: $fontFamily;
  color: $fontColor;
  background-color: $backgroudColor;
  width: 100%;
  font-size: max(calc(16/va.$sp-design-width*100vw),16px);
  line-height: calc(13/10);
  letter-spacing: calc(100/1000*1em);
  font-weight: 500;
  display: inline-block;
  text-align: center;
  line-height: calc(23/16);
  letter-spacing: calc(100/1000*1em);
  padding: max(calc(40/va.$sp-design-width*100vw),40px) 0;
}

@mixin button-main-color($fontFamily:va.$serif,$fontColor:#fff,$backgroudColor:va.$main-color){
  font-family: $fontFamily;
  color: $fontColor;
  background-color: $backgroudColor;
  width: 100%;
  font-size: max(calc(16/va.$sp-design-width*100vw),16px);
  line-height: calc(13/10);
  letter-spacing: calc(100/1000*1em);
  font-weight: 500;
  display: inline-block;
  text-align: center;
  line-height: calc(23/16);
  letter-spacing: calc(100/1000*1em);
  padding: max(calc(40/va.$sp-design-width*100vw),40px) 0;
}